@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");
@import url("../../__variables.scss");
html, body {
  margin: 0;
  padding: 0;
}

@media only screen and (min-width: 480px) {
  .homepage {
    -webkit-box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.8);
            box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.8);
    background-image: url("../../../public/background.png");
    background-size: cover;
    height: 80vh;
    width: 100%;
    font-family: 'Poppins',sans-serif;
  }
  .homepage .container {
    width: 70%;
    margin-left: 15%;
    height: 100%;
  }
  .homepage .container .row {
    width: 100%;
    height: 100%;
  }
  .homepage .container .row .col-2 {
    width: 50%;
    float: left;
    position: relative;
    height: 100%;
  }
  .homepage .container .row .col-2 .content {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 100%;
  }
  .homepage .container .row .col-2 .content h1 {
    color: #7076FE;
  }
  .homepage .container .row .col-2 .content h3 {
    font-size: 1.6rem;
    color: white;
    font-weight: 500;
  }
  .homepage .container .row .col-2 .content h2 {
    background-color: #7076FE;
    color: white;
    font-weight: 600;
    font-size: 30px;
    padding: 10px;
    width: 63%;
    border-radius: 10px;
  }
  .homepage .container .row .col-2 .content button {
    font-size: 24px;
    padding: 5px 45px;
    border-radius: 10px;
    background-color: #7076FE;
    color: white;
    font-weight: 600;
    cursor: pointer;
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
  .homepage .container .row .col-2 .content button:hover {
    background-color: rgba(246, 87, 4, 0.5);
  }
}

@media only screen and (max-width: 480px) {
  .homepage {
    -webkit-box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.8);
            box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.8);
    background-image: url("../../../public/background.png");
    background-size: cover;
    width: 100%;
    font-family: 'Poppins',sans-serif;
    height: 92vh;
  }
  .homepage .container {
    width: 90%;
    margin-left: 5%;
    height: 100%;
  }
  .homepage .container .row {
    width: 100%;
    height: 100%;
  }
  .homepage .container .row .col-2 {
    width: 100%;
    float: left;
    position: relative;
    height: 100%;
  }
  .homepage .container .row .col-2 .content {
    margin-top: 3rem;
    width: 100%;
  }
  .homepage .container .row .col-2 .content h1 {
    color: #7076FE;
    line-height: 42px;
  }
  .homepage .container .row .col-2 .content h3 {
    font-size: 1.2rem;
    color: white;
    font-weight: 500;
  }
  .homepage .container .row .col-2 .content h2 {
    background-color: #7076FE;
    color: white;
    font-weight: 600;
    font-size: 30px;
    padding: 10px;
    width: 90%;
    border-radius: 10px;
  }
  .homepage .container .row .col-2 .content button {
    font-size: 24px;
    padding: 5px 45px;
    border-radius: 10px;
    background-color: #7076FE;
    color: white;
    font-weight: 600;
    cursor: pointer;
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
  .homepage .container .row .col-2 .content button:hover {
    background-color: rgba(246, 87, 4, 0.5);
  }
}
