@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');
$primary: #7076FE;
$secondary: #4B556B;
$white: #F9F9F9;

html, body{
    margin:0;
    box-sizing: border-box;
    padding:0;
}

@media only screen and (min-width:480px){
    header{
        display: flex;
        align-items:center;
        justify-content: space-between;
        height: 80px;
        margin:auto;
        box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
        nav{
    
            ul{
                margin-right:15px;
                li{
                    float:left;
                    list-style-type:none;
                    text-decoration:none;
                    color:$secondary;
                    font-family:'Poppins', sans-serif;
                    font-weight:500;
                    font-size:21px;
                    padding:0 1.5rem;
                }
    
                button{
                    background-color:$primary;
                    border-radius:5px;
                    padding-left:8px;
                    padding-right:8px;
                    font-size:21px;
                    border:none;
                    box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                    color:$white;
                    font-weight:500;
                    padding:5px 12px;
                    margin-top:-2px;
                }
            }
        }
    }
    
}



@media only screen and (max-width:480px){
    header{
        display: flex;
        align-items:center;
        justify-content: space-between;
        height: 70px;
        margin:auto;
        box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
        nav{
    
            ul{
                margin-right:15px;
                display:none;
                
                li{
                    float:left;
                    list-style-type:none;
                    text-decoration:none;
                    color:$secondary;
                    font-family:'Poppins', sans-serif;
                    font-weight:500;
                    font-size:21px;
                    padding:0 1.5rem;
                }
    
                button{
                    background-color:$primary;
                    border-radius:5px;
                    padding-left:8px;
                    padding-right:8px;
                    font-size:21px;
                    border:none;
                    box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                    color:$white;
                    font-weight:500;
                    padding:5px 12px;
                    margin-top:-2px;
                }
            }
        }
    }
    
}
