@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');
@import url('../../__variables.scss');

html, body{
    margin:0;
    padding:0;
}

$primary: #7076FE;
$secondary: #4B556B;
$white: #F9F9F9;

@media only screen and (min-width:480px){
    .homepage{
        box-shadow: inset 0 0 0 1000px rgba(0,0,0,.8);
        background-image:url('../../../public/background.png');
        background-size:cover;
        height:80vh;
        width:100%;
        font-family:'Poppins',sans-serif;
    
        .container{
            width:70%;
            margin-left:15%;
            height:100%;
            
        .row{
            width:100%;
            height:100%;
            .col-2{
                width:50%;
                float:left;
                position: relative;
                height:100%;
    
                .content{
                    position: absolute;
                    top:50%;
                    left:50%;
                    transform: translate(-50%, -50%);
                    width:100%;
    
                    h1{
                        color:$primary;
                    }
        
                    h3{
                        font-size:1.6rem;
                        color:white;
                        font-weight:500;
                    }
        
                    h2{
                        background-color:$primary;
                        color:white;
                        font-weight:600;
                        font-size:30px;
                        padding:10px;
                        width:63%;
                        border-radius:10px;
                    }
        
                    button{
                        font-size:24px;
                        padding:5px 45px;
                        border-radius:10px;
                        background-color:$primary;
                        color:white;
                        font-weight:600;    
                        cursor:pointer;
                        transition:0.5s;
                        &:hover{
                            background-color:rgba(246,87,4,0.5);
                        }
        
                    }
                }
               
            }
        }
    }        
    }
}


@media only screen and (max-width:480px){
    .homepage{
        box-shadow: inset 0 0 0 1000px rgba(0,0,0,.8);
        background-image:url('../../../public/background.png');
        background-size:cover;
        width:100%;
        font-family:'Poppins',sans-serif;
        height:92vh;

        .container{
            width:90%;
            margin-left:5%;
            height:100%;
            
        .row{
            width:100%;
            height:100%;

            .col-2{
                width:100%;
                float:left;
                position: relative;
                height:100%;
    
                .content{
                    margin-top:3rem;
                    width:100%;
    
                    h1{
                        color:$primary;
                        line-height:42px;
                    }
        
                    h3{
                        font-size:1.2rem;
                        color:white;
                        font-weight:500;
                    }
        
                    h2{
                        background-color:$primary;
                        color:white;
                        font-weight:600;
                        font-size:30px;
                        padding:10px;
                        width:90%;
                        border-radius:10px;
                    }
        
                    button{
                        font-size:24px;
                        padding:5px 45px;
                        border-radius:10px;
                        background-color:$primary;
                        color:white;
                        font-weight:600;    
                        cursor:pointer;
                        transition:0.5s;
                        &:hover{
                            background-color:rgba(246,87,4,0.5);
                        }
        
                    }
                }
               
            }
        }
    }        
    }
}